import React from 'react';
import { Carousel } from 'antd';

import slide1 from '../assets/images/slide-1.jpg';
import slide2 from '../assets/images/slide-2.jpg';
import slide3 from '../assets/images/slide-3.jpg';
import slide4 from '../assets/images/slide-4.jpg';

const contentStyle = {
    height: '30%',
    width:'100%',
    color: '#fff',
    lineHeight: '160px',
    textAlign: 'center',
    background: '#364d79',
};
function Home() {
    const RenderSlider = () => {
        return (
            <Carousel autoplay>
                <div>
                    <img src={slide1} alt="Slide 1" style={contentStyle} />
                </div>
                <div>
                    <img src={slide2} alt="Slide 2" style={contentStyle} />
                </div>
                <div>
                    <img src={slide3} alt="Slide 3" style={contentStyle} />
                </div>
                <div>
                    <img src={slide4} alt="Slide 3" style={contentStyle} />
                </div>
            </Carousel>
        );
    };

    return <div>{RenderSlider()}</div>;
}

export default Home;
